html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
menu,
ol,
pre,
ul,
figure,
dl,
dd,
body,
blockquote {
  margin: 0;
  padding: 0;
}

body {
  color: black;
}

a {
  color: black;
}

a:visited:active {
  color: black;
}

input,
button {
  background-color: white;
  border: 1px solid black;
}

* {
  font-family: "Caveat", cursive;
  font-weight: 300;
  letter-spacing: 1.8px;
}

body {
  overflow-x: hidden;
  background: #f7d5b8;
}

.innerHeaderW {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}

.innerHeaderH {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}

.down {
  font-size: 40px !important;
  position: absolute;
  z-index: 1000;
  bottom: 6%;
  left: 50%;
  transform: translate(-50%);
  opacity: 0;
  display: block;
  color: #333333;
  cursor: pointer;
}

.pizzaHeader {
  background: #d3eefe;
  width: 100%;
  height: 100vh;
  z-index: -1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-bottom-left-radius: 60px;
  border-bottom-right-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
  position: relative;
}

.pizzaHeaderBG {
  margin: 0 auto;
  position: absolute;
  z-index: 0;
  object-fit: none;
  display: block;
  top: 50%;
  transform: translate(0, -50%);
  margin-left: 20px;
}

.pizzaHeaderProducts {
  object-fit: contain;
  position: absolute;
  top: 50%;
  left: 50%;
}

.pizzaHeaderProducts__product {
  position: absolute;
  z-index: 0;
  object-fit: contain;
}

.pizzaHeaderProducts__product-1 {
  top: -250px;
  left: -150px;
  transform: rotate(5deg);
}

.pizzaHeaderProducts__product-2 {
  top: -190px;
  left: -230px;
}

.pizzaHeaderProducts__product-3 {
  top: -180px;
  left: -155px;
  transform: scale(0.95);
}

.pizzaHeaderProducts__product-4 {
  top: -70px;
  left: -160px;
}

.pizzaHeaderProducts__product-5 {
  top: -60px;
  left: -230px;
}

.pizzaHeaderProducts__product-6 {
  top: -90px;
  left: -270px;
  transform: rotate(-8deg);
}

.pizzaHeaderProducts__product-7 {
  top: -25px;
  left: -170px;
}

.pizzaHeaderProducts__product-8 {
  top: 45px;
  left: -240px;
}

.pizzaHeaderProducts__product-9 {
  top: 85px;
  left: -175px;
  transform: rotate(-2deg);
}

.main {
  width: 100%;
}

.title {
  position: absolute;
  top: 0;
  z-index: 2;
  margin: 15px auto 0;
}

.title .letter {
  font-size: 36px;
  text-shadow: 1px 1px 3px black;
  color: #1f1f1f;
}

.textTitle {
  font-size: 40px;
  text-align: center;
  text-shadow: 1px 1px 2px black;
  margin-bottom: 90px;
  margin-top: 90px;
}

.textTitle__title {
  font-size: 36px;
}

.textTitle__subtitle {
  font-size: 26px;
}

.pizzaSlice {
  margin-top: 90px;
  margin-bottom: 90px;
  width: auto;
  height: 110vh;
  min-height: 660px;
  background: #d3eefe;
  border-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.pizzaSlice__item {
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.pizzaSlice__item-6 {
  top: 50%;
  left: 53%;
  margin-top: -80px;
}

.pizzaSlice__item-5 {
  top: 49.5%;
  left: 53%;
}

.pizzaSlice__item-4 {
  top: 49.5%;
  left: 53.5%;
}

.pizzaSlice__item-3 {
  top: 49.5%;
  left: 56.5%;
}

.pizzaSlice__item-2 {
  top: 50%;
  left: 57%;
}

.wrap {
  max-width: 1200px;
  width: 100%;
  height: 100vh;
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.slicesW {
  height: 100vh;
  width: 100%;
  position: relative;
}

.slicesH {
  max-width: 1200px;
  position: relative;
  height: 100vh;
  top: -5%;
}

.textFlex {
  max-width: 40%;
  width: 100%;
  position: relative;
  left: 0%;
  font-size: 30px;
  margin-left: 2.5%;
}

.pizza {
  width: 100%;
  height: auto;
  background: #d3eefe;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.pizza__item {
  position: relative;
  object-fit: contain;
  width: 100%;
  max-width: 1200px;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
}

.btn {
  position: absolute;
  background: rgba(195, 229, 248, 0.712);
  border: none;
  color: black;
  padding: 15px 40px;
  font-size: 60px;
  outline: none;
  margin-top: 20px;
  font-weight: bold;
  box-shadow: 1px 1px 15px 5px rgba(0, 0, 0, 0.473);
  transition: all 0.35s ease !important;
  cursor: pointer;
  border-radius: 100px;
}

.btn:hover {
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.377);
  box-shadow: 1px 1px 35px 5px rgba(33, 43, 46, 0.781);
  background: rgba(195, 229, 248, 0.863);
}

.second-section {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  background: #d3eefe;
  border-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
}

.second-section .images-wrapper {
  position: absolute;
  display: flex;
  padding: 100px 0;
  top: 20%;
  overflow: hidden;
}

.text1,
.text2 {
  z-index: 1;
  font-size: 22px;
  width: 80%;
  max-width: 800px;
  text-align: center;
  margin: 100px auto;
  position: relative;
}

.text1 {
  margin-bottom: 400px;
}

.text2 {
  margin-top: 0px;
  bottom: 0;
}

.image-container {
  overflow: hidden;
  height: 100%;
}

html {
  overflow: scroll;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */
}

/* Optional: show position indicator in red */

::-webkit-scrollbar-thumb {
  background: #ff0000;
}

@media only screen and (max-width: 1420px) {
  .section_2_05 {
    display: none;
  }

  .section_2_10 {
    display: none;
  }
}

@media only screen and (max-width: 1070px) {
  .section_2_02 {
    display: none;
  }

  .section_2_07 {
    display: none;
  }
}

