  }

  .pizzaSlice__item-5 {
    left: 42%;
  }

  .pizzaSlice__item-4 {
    left: 44%;
  }

  .pizzaSlice__item-3 {
    left: 47%;
  }

  .pizzaSlice__item-2 {
    left: 47%;
  }

  .wrap {
    flex-direction: column;
    justify-content: center;
  }

  .slicesH {
    top: -15%;
  }

  .textFlex {
    font-size: 24px;
    order: 2;
    text-align: center;
    margin: 0;
    max-width: 80%;
    top: -20%;
  }

  .second-section .images-wrapper {
    top: 24%;
  }
}

