@import "./helpers/normalize.scss";
@import "./helpers/smart-grid.scss";
@import "./helpers/variables.scss";
@import "./helpers/mixins.scss";
@import "./helpers/fonts.scss";
@import "../modules/header/header.scss";
@import "../modules/sidebar/sidebar.scss";
@import "../modules/footer/footer.scss";
* {
  font-family: "Caveat", cursive;
  font-weight: 300;
  letter-spacing: 1.8px;
}

body {
  overflow-x: hidden;
  background: rgb(247, 213, 184);
}

.innerHeaderW {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}

.innerHeaderH {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}

.down {
  font-size: 40px !important;
  position: absolute;
  z-index: 1000;
  bottom: 6%;
  left: 50%;
  transform: translate(-50%);
  opacity: 0;
  display: block;
  color: rgb(51, 51, 51);
  cursor: pointer;
  @media only screen and (max-width: 850px) {
    &:before {
      display: none;
    }
  }
  // transition: all 0.1s ease;
}

.pizzaHeader {
  background: rgb(211, 238, 254);
  width: 100%;
  height: 100vh;
  z-index: -1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-bottom-left-radius: 60px;
  border-bottom-right-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
  position: relative;
  @media only screen and (max-width: 767px) {
    border-bottom-left-radius: 35px;
    border-bottom-right-radius: 35px;
  }
  &BG {
    margin: 0 auto;
    position: absolute;
    z-index: 0;
    // width: 100%;
    object-fit: none;
    display: block;
    top: 50%;
    // left: 0;
    transform: translate(0, -50%);
    margin-left: 20px;
    @media only screen and (max-width: 767px) {
      // max-width: 90vw;
    }
  }
}

.pizzaHeaderProducts {
  @media only screen and (max-width: 700px) {
    //transform: scale(0.9);
  }
  object-fit: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  &__product {
    position: absolute;
    z-index: 0;
    object-fit: contain;
    // width: 10vw;
    &-1 {
      top: -250px;
      left: -150px;
      transform: rotate(5deg);
    }
    &-2 {
      top: -190px;
      left: -230px;
    }
    &-3 {
      top: -180px;
      left: -155px;
      transform: scale(0.95);
    }
    &-4 {
      top: -70px;
      left: -160px;
    }
    &-5 {
      top: -60px;
      left: -230px;
    }
    &-6 {
      top: -90px;
      left: -270px;
      transform: rotate(-8deg);
    }
    &-7 {
      top: -25px;
      left: -170px;
    }
    &-8 {
      top: 45px;
      left: -240px;
    }
    &-9 {
      top: 85px;
      left: -175px;
      transform: rotate(-2deg);
    }
  }
}

.main {
  width: 100%;
}

.title {
  position: absolute;
  top: 0;
  z-index: 2;
  margin: 15px auto 0;
  @media only screen and (max-width: 767px) {
    margin-top: 5px;
  }
}

.title .letter {
  font-size: 36px;
  text-shadow: 1px 1px 3px black;
  color: rgb(31, 31, 31);
  @media only screen and (max-width: 767px) {
    font-size: 28px;
  }
}

.textTitle {
  font-size: 40px;
  text-align: center;
  text-shadow: 1px 1px 2px black;
  margin-bottom: 90px;
  margin-top: 90px;
  &__title {
    font-size: 36px;
    @media only screen and (max-width: 767px) {
      font-size: 28px;
    }
  }
  &__subtitle {
    font-size: 26px;
  }
}

.pizzaSlice {
  margin-top: 90px;
  margin-bottom: 90px;
