    }
    @media only screen and (max-width: 600px) {
      left: 41%;
    }
    @media only screen and (max-width: 500px) {
      left: 40%;
    }
    @media only screen and (max-width: 400px) {
      left: 38%;
    }
  }
  &__item-4 {
    top: 49.5%;
    left: 53.5%;
    @media only screen and (max-width: 950px) {
      left: 44%;
    }
    @media only screen and (max-width: 600px) {
      left: 43%;
    }
    @media only screen and (max-width: 500px) {
      left: 42%;
    }
    @media only screen and (max-width: 400px) {
      left: 40%;
    }
  }
  &__item-3 {
    top: 49.5%;
    left: 56.5%;
    @media only screen and (max-width: 950px) {
      left: 47%;
    }
    @media only screen and (max-width: 600px) {
      left: 46%;
    }
    @media only screen and (max-width: 500px) {
      left: 45%;
    }
    @media only screen and (max-width: 400px) {
      left: 44%;
    }
  }
  &__item-2 {
    top: 50%;
    left: 57%;
    @media only screen and (max-width: 950px) {
      left: 47%;
    }
    @media only screen and (max-width: 600px) {
      left: 46%;
    }
    @media only screen and (max-width: 500px) {
      left: 45%;
    }
    @media only screen and (max-width: 400px) {
      left: 44%;
    }
  }
  //  &__item-1 {
  //      top: 50%;
  //      transform: translate(0%, -50%);
  //      left: 29%;
  //      position: absolute;
  //  }
