.wrap {
  max-width: 1200px;
  // width: 100vw;
  width: 100%;
  height: 100vh;
  // min-height: 890px;
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media only screen and (max-width: 950px) {
    flex-direction: column;
    justify-content: center;
  }
}

.slicesW {
  height: 100vh;
  width: 100%;
  position: relative;
}

.slicesH {
  max-width: 1200px;
  position: relative;
  height: 100vh;
  top: -5%;
  @media only screen and (max-width: 950px) {
    top: -15%;
  }
  @media only screen and (max-width: 800px) {
    margin-top: 20px;
  }
}

.textFlex {
  max-width: 40%;
  width: 100%;
  position: relative;
  left: 0%;
  font-size: 30px;
  margin-left: 2.5%;
  @media only screen and (max-width: 950px) {
    font-size: 24px;
    order: 2;
    text-align: center;
    margin: 0;
    max-width: 80%;
    top: -20%;
    //   top: 3%;
  }
  @media only screen and (max-height: 800px) {
    margin-top: 20px;
  }
  @media only screen and (max-height: 600px) {
    margin-top: 30px;
  }
  @media only screen and (max-height: 450px) {
    margin-top: 50px;
  }
  @media only screen and (max-height: 400px) {
    margin-top: 70px;
  }
  @media only screen and (max-height: 350px) {
    margin-top: 90px;
  }
  @media only screen and (max-width: 450px) {
    font-size: 18px;
    top: -25%;
  }
}

.pizza {
  width: 100%;
  height: auto;
  //    min-height: 890px;
  background: rgb(211, 238, 254);
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  @media only screen and (max-width: 767px) {
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    padding-bottom: 15px;
  }
  &__item {
    position: relative;
    object-fit: contain;
    width: 100%;
    max-width: 1200px;
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    // box-shadow: 1px 1px 15px 1px rgb(126, 158, 161);
  }
}

.btn {
  position: absolute;
  background: rgba(195, 229, 248, 0.712);

  border: none;
  color: rgb(0, 0, 0);
  padding: 15px 40px;
  font-size: 60px;
  outline: none;
  margin-top: 20px;
  font-weight: bold;
  box-shadow: 1px 1px 15px 5px rgba(0, 0, 0, 0.473);
  // box-shadow: 1px 1px 35px 5px rgb(0, 0, 0);
  transition: all 0.35s ease !important;
  cursor: pointer;
  border-radius: 100px;
  //border: 3px solid rgba(0, 0, 0, 0.726);
  &:hover {
    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.377);
    box-shadow: 1px 1px 35px 5px rgba(33, 43, 46, 0.781);

    background: rgba(195, 229, 248, 0.863);
  }
  @media only screen and (max-width: 767px) {
    font-size: 40px;
    //   border: 3px solid orange;
  }
  @media only screen and (max-width: 500px) {
    margin-top: 10px;
    font-size: 30px;
    padding: 15px 30px;
  }
  @media only screen and (max-width: 450px) {
    font-size: 26px;
    padding: 10px 20px;
  }
}

.second-section {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  background: rgb(211, 238, 254);
  border-radius: 60px;
  @media only screen and (max-width: 767px) {
    border-radius: 35px;
  }
  box-shadow: 1px 1px 15px 1px rgba(56, 56, 56, 0.459);
}

.second-section .images-wrapper {
  position: absolute;
  display: flex;
  padding: 100px 0;
  top: 20%;
  overflow: hidden;
  @media only screen and (max-width: 950px) {
    top: 24%;
  }
  @media only screen and (max-width: 600px) {
    top: 20%;
  }
  @media only screen and (max-width: 450px) {
    top: 10%;
  }
}

.text1,
.text2 {
